import React, { Component } from "react";

import { Button, Popconfirm, Tooltip, Form } from "antd";
import EditableCell from "./editableCellExpense";
import EditableContext from "./editableContext";
import "../../../App/editableStyle.css";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import NumberFormat from "../../../App/NumberFormat";
import CardWrapper from "../../../App/app.style";
// import ModalDetail from "./modalDetail";
import getLanguage from "../../../App/MyLanguange";

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

const getTotal = arr => {
  let totalAmount = 0;
  let totalRatio = 0;
  arr.map(data => {
    totalAmount += parseFloat(data.allocated_amount);
    totalRatio += parseFloat(data.allocated_ratio);
  });

  return {
    totalAmount,
    totalRatio
  };
};

class EditableTable extends Component {
  constructor(props) {
    super(props);
    const { expenses } = props;

    this.state = {
      dataSource: [...expenses],
      count: expenses.length + 1,
      totalAmount: 0,
      visible: false,
      selectedItem: {}
    };
  }

  componentDidMount() {
    const { 
      temp_expense, 
      temp_expenses, 
      clearExpense, 
      clearExpenses,
    } = this.props;

    // milih expense sendiri
    if (temp_expense) {
      const {
        dataSource,
        count,
        totalAmount,
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_account === temp_expense.id_account
      );

      // expense baru
      if (find < 0) {
        const init = {
          index_no: count,
          allocated_amount: 0,
          allocated_ratio: 0
        };

        const newData = Object.assign({}, temp_expense, init);
        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalAmount: totalAmount,
        });

        clearExpense(
          [...dataSource, newData],
          totalAmount
        );
        // console.log(this.props.expense);
      }
    }
    // jika temp dari transaksi sebelumnya
    else if (temp_expenses.length !== 0) {
      let newTotAmount = 0;
      let newIndex = 0;

      temp_expenses.map(currData => {
        newTotAmount += parseFloat(currData.allocated_amount);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...temp_expenses],
        totalAmount: newTotAmount,
        count: newIndex
      });
      clearExpenses(temp_expenses, newTotAmount );
    }
  }

  componentDidUpdate(prevProps) {
    const { 
      temp_expense, 
      temp_expenses, 
      clearExpense, 
      clearExpenses,
    } = this.props;

    // milih expense sendiri
    if (temp_expense) {
      const {
        dataSource,
        count,
        totalAmount,
      } = this.state;

      const find = dataSource.findIndex(
        currData => currData.id_account === temp_expense.id_account
      );

      // expense baru
      if (find < 0) {
        const init = {
          index_no: count,
          allocated_amount: 0,
          allocated_ratio: 0,
        };

        const newData = Object.assign({}, temp_expense, init);
        // console.log(newData);

        this.setState({
          dataSource: [...dataSource, newData],
          count: count + 1,
          totalAmount: totalAmount,
        });

        clearExpense(
          [...dataSource, newData],
          totalAmount,
        );
        // console.log(this.props.expense);
      }
    }
    // jika expense dari transaksi sebelumnya
    else if (temp_expenses.length !== 0) {
      let newTotAmount = 0;
      let newIndex = 0;

      temp_expenses.map(currData => {
        newTotAmount += parseFloat(currData.allocated_amount);
        newIndex = parseInt(currData.index_no) + 1;
      });

      this.setState({
        dataSource: [...temp_expenses],
        totalAmount: newTotAmount,
        count: newIndex
      });
      clearExpenses(temp_expenses, newTotAmount);
    }
    // else if(prevProps.currency_rate !== this.props.currency_rate ){
    //   this.props.expenses.map(())
    // }
  }

  handleDetailModal = row => {
    const { visible } = this.state;
    // console.log(row);

    visible
      ? this.setState({ visible: false, selectedItem: {} })
      : this.setState({ visible: true, selectedItem: row });
  };

  handleDetail = value => {
    this.setState({ visible: false, selectedItem: {} });
    this.handleSave(value);
  };

  handleDelete = index_no => {
    const { handleSave } = this.props;
    let newTotAmount = 0;
    let newTotCurrAmount = 0;

    const dataSource = this.state.dataSource;

    let newData = dataSource.filter(currData => currData.index_no !== index_no);
    newData.map((currData, index) => {
      currData.index_no = index + 1;
      newTotAmount += parseFloat(currData.allocated_amount);
    });

    this.setState({
      dataSource: [...newData],
      count: newData.length + 1,
      totalAmount: newTotAmount,
    });
    handleSave(newData, newTotAmount );
  };

  handleSave = (row, name) => {
    const { total_amount } = this.props;
    const newData = this.state.dataSource;
    let newTotAmount = 0;

    const index = newData.findIndex(
      currData => row.index_no === currData.index_no
    );

    if(name === "allocated_ratio"){
      row.allocated_amount = parseFloat(row.allocated_ratio) * total_amount / 100;
    }

    newData.splice(index, 1, {
      ...newData[index],
      ...row
    });

    // console.log(newData)

    newData.map(currData => {
      newTotAmount += parseFloat(currData.allocated_amount);
    });


    this.setState({
      dataSource: [...newData],
      totalAmount: newTotAmount,
    });
    this.props.handleSave(newData, newTotAmount);
  };

  render() {
    let columns = [
      {
        title: getLanguage({id: "no."}),
        dataIndex: "index_no",
        width: "5%",
        align: "right",
        render: (text, row) => {
          if (isNaN(text)) {
            return {
              children: text,
              props: {
                colSpan: 3
              }
            };
          } else {
            return {
              children: text,
              props: {
                colSpan: 1
              }
            };
          }
        }
      },
      {
        title: getLanguage({id: "accountCode"}),
        dataIndex: "account_code",
        width: "30%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "accountName"}),
        dataIndex: "account_name",
        width: "45%",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 0
              }
            };
          }
          if (text && text.length > 50) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 50) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({id: "baseAmount"}),
        dataIndex: "allocated_amount",
        width: "20%",
        align: "right",
        //editable: true,
        render: text => {
          if (isNaN(text)) {
            return <span> {text} </span>
          } 
          else {
            return <NumberFormat value={text} />
          }
        }
      },
      {
        title: getLanguage({id: "ratio%"}),
        dataIndex: "allocated_ratio",
        width: "5%",
        align: "right",
        onCell: record => {
          if(isNaN(record.index_no)){
            return {
              record,
              dataIndex: "allocated_ratio",
              title: getLanguage({id: "ratio%"}),
              width: "15%",
              status: this.props.status
            }
          }
          else{
            return {
              record,
              editable: true,
              dataIndex: "allocated_ratio",
              title: getLanguage({id: "ratio%"}),
              status: this.props.status,
              handleSave: this.handleSave
            }   
          }
        }
      },
      {
        title: "",
        dataIndex: "operation",
        width: "15%",
        align: "center",
        fixed: "right",
        render: (text, row) => {
          if (isNaN(row.index_no)) {
            return {
              props: {
                colSpan: 1
                // fixed: null
              }
            };
          }
          if (!this.props.status || this.props.status === 1) {
            return (
              <div>
                <Popconfirm
                  title={<IntlMessages id="confirmDelete" />}
                  okText={getLanguage({ id: "yes" })}
                  cancelText={getLanguage({ id: "no" })}
                  onConfirm={() => this.handleDelete(row.index_no)}
                >
                  <Tooltip
                    title={<IntlMessages id="delete" />}
                  >
                    <Button
                      //style={{ borderRadius: "50%" }}
                      className="invoiceDltBtn"
                      size="small"
                    >
                      <i className="ion-trash-b" />
                    </Button>
                  </Tooltip>
                </Popconfirm>
              </div>
            );
          }
        }
      }
    ];
    // console.log(this.state.dataSource);
    const { total_amount } =this.props;
    const { dataSource, visible, selectedItem } = this.state;
    const components = {
      body: {
        row: EditableFormRow,
        cell: EditableCell
      }
    };

    const total = getTotal(dataSource);
    const info = {
      
      index_no: "Total",
      allocated_amount: total.totalAmount,
      allocated_ratio: total.totalRatio
      // allocated_ratio: isNaN(100*parseFloat(total.totalAmount)/total_amount)
      //   ? 0
      //   : (100*parseFloat(total.totalAmount)/parseFloat(total_amount))
    };
    const modifiedData = dataSource.length > 0 ? [...dataSource, info] : [];
    //console.log(info, total, dataSource);
    return (
      <CardWrapper>  
        <div>
          <Scrollbars style={{ width: "100%" }}>
            <TableWrapper
              className="invoiceListTable"
              components={components}
              rowKey={record => record.index_no}
              rowClassName={() => "editable-row"}
              dataSource={modifiedData}
              columns={columns}
              pagination={false}
              // scroll={{ x: 1300 }}
            />
          </Scrollbars>
        </div>
      </CardWrapper>
    );
  }
}

export default EditableTable;
