import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import JobCostRolloverCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/jobCostRollover/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";

const TabPane = Tabs.TabPane;

const redirectTo = "/dashboard/inventories/job-cost-rollovers";

class CreateJobCostRollover extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createJobCostRollover, getImportJobCostings } = this.props;
    createJobCostRollover();
    getImportJobCostings();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.jobCostRollover.error) {
      notification("error", this.props.jobCostRollover.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.jobCostRollover.success) {
      this.props.history.replace(
        redirectTo +
          "/update/" +
          this.props.jobCostRollover.id_job_cost_rollover
      );
    }
  }

  handleSubmit = value => {
    const { storeJobCostRollover } = this.props;
    storeJobCostRollover(value);
  };

  render() {
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="jobCostRollovers.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane
              tab={<IntlMessages id="jobCostRollovers.tab.detail" />}
              key="1"
            >
              <JobCostRolloverCreateForm
                submit={this.handleSubmit}
                jobCostRollover={this.props.jobCostRollover}
                permissions={permissions}
                getItems={this.props.getItems}
                history={this.props.history}
              />
            </TabPane>
            <TabPane
              tab={<IntlMessages id="jobCostRollovers.tab.attachment" />}
              key="2"
              disabled
            ></TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    jobCostRollover: state.jobCostRollover,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  CreateJobCostRollover
);
