const actions = {
  getJobCostRollovers: (page, search, filter) => ({
    type: 'GET_JOB_COST_ROLLOVERS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getImportJobCostings: (page, search, filter) => ({
    type: 'GET_IMPORT_JOB_COSTINGS_JCR_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getItems: (id_location, search) => ({
    type: "GET_FILTER_ITEMS_JCR_REQUEST",
    payload: { id_location: id_location, search: search }
  }),
  createJobCostRollover: () => ({
    type: 'CREATE_JOB_COST_ROLLOVER_REQUEST'
  }),
  storeJobCostRollover: data => ({
    type: 'STORE_JOB_COST_ROLLOVER_REQUEST',
    payload: { body: data }
  }),
  editJobCostRollover: id_job_cost_rollover => ({
    type: 'EDIT_JOB_COST_ROLLOVER_REQUEST',
    payload: { id_job_cost_rollover: id_job_cost_rollover }
  }),
  updateJobCostRollover: (id_job_cost_rollover, data) => ({
    type: 'UPDATE_JOB_COST_ROLLOVER_REQUEST',
    payload: {
      id_job_cost_rollover: id_job_cost_rollover,
      body: data
    }
  }),
  showJobCostRollover: id_job_cost_rollover => ({
    type: 'SHOW_JOB_COST_ROLLOVER_REQUEST',
    payload: { id_job_cost_rollover: id_job_cost_rollover }
  }),
  processJobCostRollover: id_job_cost_rollover => ({
    type: "PROCESS_JOB_COST_ROLLOVER_REQUEST",
    payload: { id_job_cost_rollover }
  }),
  cancelJobCostRollover: (id_job_cost_rollover, data) => ({
    type: "CANCEL_JOB_COST_ROLLOVER_REQUEST",
    payload: { 
      id_job_cost_rollover,
      body : data
    }
  }),
  uploadDocument: (id_job_cost_rollover, data) => ({
    type: "UPLOAD_JOB_COST_ROLLOVER_DOCUMENT_REQUEST",
    payload: {
      id_job_cost_rollover,
      body : data
    }
  }),
  downloadDocument: id_job_cost_rollover_document => ({
    type: "DOWNLOAD_JOB_COST_ROLLOVER_DOCUMENT_REQUEST",
    payload: {id_job_cost_rollover_document}
  }),
  deleteDocument: id_job_cost_rollover_document => ({
    type: "DELETE_JOB_COST_ROLLOVER_DOCUMENT_REQUEST",
    payload: { id_job_cost_rollover_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
